.content-columns {
  padding-top: 50px;
  padding-bottom: 50px;
}

.content-columns .light h1, .content-columns .light h2, .content-columns .light h3, .content-columns .light p {
  color: #FFFFFF;
}

.content-columns .dark h1, .content-columns .dark h2 {
  color: #222;
}

.content-columns .dark h3, .content-columns .dark p {
  color: #444;
}
