.half-width-content-image-bg {
  background-size: cover !important;
  background-position: center center;
}

.half-width-content-image-bg .content-container {
  /*background-color: rgba(0,0,0,.4);*/
  background-color: #FFF;
  padding: 40px;
}

.half-width-content-image-bg h2 {
  margin-top: 0;
}

.half-width-content-image-bg h3 {
  font-size: 1em;
  /*color: #EEE;*/
}

.half-width-content-image-bg .action-buttons .btn {
  margin-bottom: 1em;
}

.half-width-content-image-bg .action-buttons .btn-not-first {
  margin-left: 0;
  margin-right: 1em;
}

.half-width-content-image-bg .content-right .action-buttons .btn-not-first {
  margin-right: 0em;
  margin-left: 1em;
}

.half-width-content-image-bg .content-right {
  text-align: right;
}

@media (max-width: 767px) {
  .half-width-content-image-bg {
    overflow-x: hidden;
  }
  .half-width-content-image-bg .container {
    padding-left: 0;
    padding-right: 0;
  }
}
