.full-screen-billboard {
  height: auto;
  text-align: center;
}

.full-screen-billboard h2 {
  margin: 0% 10%;
  margin-bottom: 0;
  padding-bottom: 1%;
  padding-left: 0%;
  text-align: left;
  font-weight: 900;
  font-size: 3.5rem;
  border-bottom: 2px solid #FFF;
}

.full-screen-billboard.light h2 {
  color: #FFFFFF;
  border-color: #FFFFFF;
}

.full-screen-billboard.light h3 {
  color: #FFFFFF;
}

.full-screen-billboard.dark h2 {
  color: #000000;
  border-color: #000000;
}

.full-screen-billboard.dark h3 {
  color: #000000;
}

.full-screen-billboard .action-buttons {
  margin-left: 10%;
  margin-top: 5vh;
}

.full-screen-billboard h3 {
  text-align: left;
  padding-left: 0%;
  margin-left: 10%;
  font-size: 42px;
}

@media (max-width: 767px) {
  .full-screen-billboard h3 {
    margin-left: 0%;
  }
  .full-screen-billboard h2 {
    margin: 0% 0;
    margin-bottom: 0;
  }
  .full-screen-billboard .action-buttons {
    margin-left: 0%;
    margin-top: 5vh;
  }
}
