.sermon {
  margin-bottom: 15px;
}

.sermon-container {
  padding: 5px 15px 0px 15px;
}

.sermon a {
  color: #5a5a5a;
}

.sermon-detail {
  font-size: 13px;
  margin: 0px 0;
  border: 1px #333 solid;
  padding: 5px;
  float: left;
}

.sermon-title {
  margin-top: 0px;
  margin-bottom: 0px;
  font-size: 24px;
  background-color: #333;
  padding: 5px;
  color: #FFFFFF;
}

.sermon-title .date {
  letter-spacing: 2px;
  font-size: 12px;
}

.sermon-detail .detail-type {
  color: #222;
  font-weight: bold;
}

.media-label {
  font-size: 10px;
  margin-left: 0px;
}

.media-links {
  float: left;
  padding: 5px 0px;
}

.media-links ul {
  float: left;
  display: inline;
  list-style-type: none;
  margin-left: 0;
  padding: 0px;
  margin-bottom: 0;
}

.media-links ul li {
  padding: 0 5px 5px 0px;
  float: left;
  display: inline;
}

.media-links a {
  float: left;
  color: #222;
  font-size: 11px;
  padding: 3px 4px;
}

.media-links a i {
  font-size: 11px !important;
  color: #222;
  padding-right: 5px;
}

@media (max-width: 767px) {
  .sermon {
    margin-bottom: 15px;
  }
  .sermon-title {
    font-size: 24px;
  }
}
