.count_down {
  margin: auto;
  margin-bottom: 80px;
  margin-top: 80px;
  overflow: hidden;
  float: none;
}

.count_down .custom {
  border-right: 1px solid #D0D0CF;
}

.count_down .custom:last-child {
  border-right: 0px solid #D0D0CF;
}

.time .counter {
  color: #FFFFFF;
  font-size: 70px;
  font-weight: 300;
  font-family: 'Oswald', sans-serif;
  display: block;
  text-align: center;
}

.time .title {
  font-family: 'Open Sans', sans-serif;
  color: #FFFFFF;
  font-weight: lighter;
  display: block;
  text-align: center;
  font-size: 18px;
}
