.action-billboard {
  background-size: cover;
}

.action-billboard .content-container {
  padding: 120px 0px;
}

.action-billboard h2 {
  font-size: 3rem;
  margin: 0;
  line-height: 1em;
}

.action-billboard h3 {
  font-size: 2rem;
  margin: 0;
  font-weight: 300;
  line-height: 1em;
  font-family: "Lato", sans-serif;
}

.action-billboard p {
  margin-top: 25px;
}

.action-billboard .action-buttons {
  padding-top: 40px;
  width: 100%;
}

.action-billboard.light h2, .action-billboard.light h3, .action-billboard.light p {
  color: #FFF;
  text-shadow: 0px 0px 3px #444;
}

.action-billboard.center * {
  text-align: center;
}

.action-billboard.dark h2 {
  color: #222;
}

.action-billboard.dark h3, .action-billboard.dark p {
  color: #444;
}
