.title-image-back {
  position: relative;
  height: 250px;
  background-size: cover;
  background-position: center center;
}

.title-image-back img {
  min-width: 100%;
}

.title-image-back .tag {
  /*background-image: linear-gradient(-48deg, rgba(170, 20, 20, 0.8) 4%, rgba(217, 199, 16, 0.8) 100%);*/
  border: 3px solid #FFFFFF;
  position: absolute;
  bottom: 15%;
  left: 5%;
  padding: 25px;
  max-width: 750px;
}

.title-image-back h1 {
  font-weight: 900;
  font-size: 50px;
  line-height: 53px;
  color: #FFFFFF;
  letter-spacing: 2px;
  margin: 0;
}

@media (max-width: 767px) {
  .title-image-back {
    height: 150px;
  }
  .title-image-back .tag {
    padding: 10px;
    left: 5%;
    right: 5%;
  }
  .title-image-back h2 {
    font-size: 28px;
    line-height: 26px;
  }
  .title-image-back h4 {
    font-size: 16px;
    line-height: 20px;
  }
}
